@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-column-group(@theme-color){
	padding: 1.92rem 0 65px 0;
	background: @container-bg-color;
	.size(100%);
	max-width:@container-max-width;
	.absolute(0,_,_,_);

	&.reset-top{
		padding-top:0px;
	}
	@slide-width:24;
	.col-slide{
		background: #fff;
		width:unit(@slide-width,%);
		float: left;
		height:100%;
		overflow-x: hidden;
		overflow-y: auto;

		.item{
			color:@text-color;
			line-height: 50px;
			padding-left:10px;
			.ellipsis();

			&-active{
				color:@theme-color;
				background: @container-bg-color;
				text-indent: -3px;
				border-left:3px solid @theme-color;

			}
		}
	}
	.col-wrap{
		height:100%;
		margin-left:unit(@slide-width,%);
		overflow-x: hidden;
		overflow-y: auto;
		padding-top:2.5%;
	}
	.col-content{


		.tlt{
			color:#999;
			padding-left:2.5%;
			margin-bottom:3.25%;
			.ellipsis();
		}
		.item-box{

			width:100%;
			overflow:hidden;

			.item{
				width:29%;
				margin-left:3.25%;
				margin-bottom:3.25%;
				&:before{
					display: block;
					content:'';
					padding-top:100%;
				}
				background: #fff;
				float: left;

				position: relative;

				.img{
					display: block;
					padding:9%;
				}
				.name{
					width:82%;
					.absolute(_,_,9%,0);
					margin:0 9%;
					background: rgba(0,0,0,8);
					font-size:@font-size-sm;
					line-height:1.6;
					text-align: center;
					color: #fff;
					.ellipsis();
				}
			}
		}
	}
}
